import { Route, BrowserRouter } from 'react-router-dom'
import routes from './components/pages/index'
import Web3 from "web3";
import React, { Component } from "react";
import Liquid from "./abis/liquid.json";
import LP from "./abis/liq-eth.json";
import LIQLP from "./abis/liquidStaker.json";
import FOG from "./abis/fog.json";
import FOGLP from "./abis/fog-eth.json";
import { Header } from "./components/layouts";
import Fog from "./components/pages/fog";

class App extends Component {

    //Connect Web 3
    async componentWillMount() {
        await this.loadWeb3();
        await this.loadBlockchainData();
        const amount = Web3.utils.toWei('1', 'ether')
        // this.stakeTokens(amount)
        // this.unstakeTokens(amount)

    }

    async loadBlockchainData() {
        const web3 = window.web3;
        const accounts = await web3.eth.getAccounts();
        this.setState({
            account: accounts[0],
        });

        console.log("Accounts: ", accounts);
        const networkId = await web3.eth.net.getId();
        console.log("network ID: ", networkId, (networkId !== 1));
        // kovan === 42
        // mainnet === 1

        const testNet = (networkId !== 1);
        this.setState({
            testNet: testNet,
        });
        //Liquid

        // const liqData = Liquid;
        // if (liqData) {
        //     const liquidAddress = (testNet) ? liqData.kovanAddress : liqData.address;
        //     const liquid = new web3.eth.Contract(Liquid.abi, liquidAddress);
        //     this.setState({liquid});
        //     let liquidBalance = await liquid.methods.balanceOf(this.state.account).call();
        //     //console.log(liquidBalance);
        //     this.setState({
        //         liquidBalance: await liquid.methods.balanceOf(this.state.account).call()
        //     });
        //     console.log("Liquid Balance: ", web3.utils.fromWei(liquidBalance, 'ether').toString());
        // } else {
        //     window.alert("Liquid contract not deployed to network");
        // }

        //LP
        const liquidLpData = LP;
        if (liquidLpData) {
            const liquidLpAddress = (testNet) ? liquidLpData.kovanAddress : liquidLpData.address;
            const liquidLpAbi = (testNet) ? liquidLpData.kovanAbi : liquidLpData.abi;
            const liquidLp = new web3.eth.Contract(liquidLpAbi, liquidLpAddress);
            this.setState({ liquidLp });
            //lp Balance
            let liquidLpBalance = await liquidLp.methods.balanceOf(this.state.account).call();
            let totalSupply = await liquidLp.methods.totalSupply().call();
            this.setState({
                liquidLpBalance: liquidLpBalance
            });

            console.log("LP Balance: ", web3.utils.fromWei(liquidLpBalance, 'ether').toString());
        } else {
            window.alert("LP contract not deployed to network");
        }

        //fog
        const fogData = FOG;
        if (fogData) {
            const fogAddress = (testNet) ? fogData.kovanAddress : fogData.address;
            const fogAbi = (testNet) ? fogData.kovanAbi : fogData.abi;
            const fog = new web3.eth.Contract(fogAbi, fogAddress);
            this.setState({ fog });
            //fog Balance
            let fogBalance = await fog.methods.balanceOf(this.state.account).call();
            let fogDensity = await fog.methods.fogDensity().call();
            console.log('fogDensity: '+fogDensity)
            this.setState({
                fogBalance: fogBalance,
                fogDensity: fogDensity
            });
            console.log("FOG Balance: ", web3.utils.fromWei(fogBalance, 'ether').toString());
        } else {
            window.alert("FOG contract not deployed to network");
        }

        const fogLPData = FOGLP;
        if (fogLPData) {
            const fogLPAddress = (testNet) ? fogLPData.kovanAddress : fogLPData.address;
            const fogLPAbi = (testNet) ? fogLPData.kovanAbi : fogLPData.abi;
            const fogLP = new web3.eth.Contract(fogLPAbi, fogLPAddress);
            this.setState({ fogLP });
            //fog Balance
            let fogLPBalance = await fogLP.methods.balanceOf(this.state.account).call();
            this.setState({
                fogLPBalance: fogLPBalance
            });
            console.log("fogLPBalance Balance: ", web3.utils.fromWei(fogLPBalance, 'ether').toString());

        } else {
            window.alert("fogLP contract not deployed to network");
        }

        const liquidLPStakerData = LIQLP;
        if (liquidLPStakerData) {
            const liquidLPStakerAddress = (testNet) ? liquidLPStakerData.kovanAddress : liquidLPStakerData.address;
            const liquidLPStakerAbi = (testNet) ? liquidLPStakerData.kovanAbi : liquidLPStakerData.abi;
            const liquidLPStaker = new web3.eth.Contract(liquidLPStakerAbi, liquidLPStakerAddress);
            this.setState({ liquidLPStaker });
            //fog Balance
            let llpReward = await liquidLPStaker.methods.getReward(this.state.account).call();
            let llpYourStakeAmount = await liquidLPStaker.methods.getStakedAmount(this.state.account).call();
            let llpRank = await liquidLPStaker.methods.getStakedRank(this.state.account).call();
            let llpRewardPool = await liquidLPStaker.methods.getRewardPoolBalance().call();
            this.setState({
                llpReward: llpReward,
                llpYourStakeAmount: llpYourStakeAmount,
                llpRank: llpRank,
                llpRewardPool: llpRewardPool
            });

        } else {
            window.alert("liquidLPStaker contract not deployed to network");
        }

        this.setState({
            loading: false,
        });
    }

    // load wallet
    async loadWeb3() {
        const testnetURL = 'https://kovan.infura.io/v3/8f0acb84a9af45cbb0213b971e6bd364';
        const provider = (this.state.testNet) ? new Web3.providers.HttpProvider(testnetURL) : window.ethereum;
        // set dev or live env:
        if (window.ethereum) {
            window.web3 = new Web3(provider);
            await window.ethereum.enable();
            this.setState({
                activeWallet: true
            })
        } else if (window.web3) {
            window.web3 = new Web3(window.web3.currentProvider);
            this.setState({
                activeWallet: true
            })
        } else {
            window.alert(
                "Non Ethereum browser detected. You should consider installing metamask!"
            );
        }
    }

    stakeTokens = async (amount) => {
        this.setState({ loading: true });
        const web3 = window.web3;

        const liquidLpAddress = (this.state.testNet) ? LP.kovanAddress : LP.address;
        const liquidLpAbi = (this.state.testNet) ? LP.kovanAbi : LP.abi;

        const liquidStakerLpAddress = (this.state.testNet) ? LIQLP.kovanAddress : LIQLP.address;
        const liquidStakerLpAbi = (this.state.testNet) ? LIQLP.kovanAbi : LIQLP.abi;

        const liqLp = new web3.eth.Contract(liquidLpAbi, liquidLpAddress);
        const lLpStaker = new web3.eth.Contract(liquidStakerLpAbi, liquidStakerLpAddress);

        try {
            // Approve
            console.log(amount);
            //liqLp is 0x7A
            await liqLp.methods.approve(liquidStakerLpAddress, amount)
                .send({ from: this.state.account })
                .on("transactionHash", async (hash) => {
                    // Stake
                    //lLpStaker is 0xD0
                    await lLpStaker.methods.stake(amount)
                        .send({ from: this.state.account })
                        .on("transactionHash", (hash) => {
                            this.setState({ loading: false });
                        })
                });
        } catch (error) {
            console.log(error)
        }
    };

    unstakeTokens = async (amount) => {
        this.setState({ loading: true });
        const web3 = window.web3;

        const liquidStakerLpAddress = (this.state.testNet) ? LIQLP.kovanAddress : LIQLP.address;
        const liquidStakerLpAbi = (this.state.testNet) ? LIQLP.kovanAbi : LIQLP.abi;

        const lLpStaker = new web3.eth.Contract(liquidStakerLpAbi, liquidStakerLpAddress);

        try {
            await lLpStaker.methods.unstake(amount)
                .send({ from: this.state.account })
                .on("transactionHash", (hash) => {
                    this.setState({ loading: false });
                })
        } catch (error) {
            console.log(error)
        }
    };

    claimRewards = async (amount) => {
        this.setState({ loading: true });
        const web3 = window.web3;

        const liquidStakerLpAddress = (this.state.testNet) ? LIQLP.kovanAddress : LIQLP.address;
        const liquidStakerLpAbi = (this.state.testNet) ? LIQLP.kovanAbi : LIQLP.abi;

        const lLpStaker = new web3.eth.Contract(liquidStakerLpAbi, liquidStakerLpAddress);
        try {
            await lLpStaker.methods.claim()
                .send({ from: this.state.account })
                .on("transactionHash", (hash) => {
                    this.setState({ loading: false });
                })
        } catch (error) {
            console.log(error)
        }
    };
    condensate = async () => {
        this.setState({ loading: true });
        const web3 = window.web3;

        const fogAddress = (this.state.testNet) ? FOG.kovanAddress : FOG.address;
        const fogAbi = (this.state.testNet) ? FOG.kovanAbi : FOG.abi;

        const fog = new web3.eth.Contract(fogAbi, fogAddress);
        try {
            await fog.methods.rewardLiquidityProviders()
                .send({ from: this.state.account })
                .on("transactionHash", (hash) => {
                    this.setState({ loading: false });
                })
        } catch (error) {
            console.log(error)
        }
    };

    constructor(props) {
        super(props);
        this.state = {
            account: "",
            amount: 0,
            activeWallet: false,
            loading: true,
            liquidBalance: 0,
            liquidLpBalance: 0,
            fogBalance: 0,
            fogDensity: 0,
            fogLpBalance: 0,
            llpReward: 0,
            llpYourStakeAmount: 0,
            llpRank: 0,
            llpRewardPool: 0
        };
    }

    render() {
        let content;
        content = (
            <div>
                <Header
                    activeWallet={this.state.activeWallet}
                />
                <Fog
                    amount={this.state.amount}
                    //liquidBalance={this.state.liquidBalance}
                    liquidLpBalance={this.state.liquidLpBalance}
                    fogBalance={this.state.fogBalance}
                    fogDensity={this.state.fogDensity}
                    fogLpBalance={this.state.fogLpBalance}
                    llpReward={this.state.llpReward}
                    llpYourStakeAmount={this.state.llpYourStakeAmount}
                    llpRank={this.state.llpRank}
                    llpRewardPool={this.state.llpRewardPool}
                    stakeTokens={this.stakeTokens}
                    unstakeTokens={this.unstakeTokens}
                    claimRewards={(this.claimRewards)}
                    condensate={(this.condensate)}
                />
            </div>
        );
        return (
            <div>
                <BrowserRouter>
                    {
                        routes.map((route, idx) => (
                            <Route exact path={route.path} component={route.component} key={idx}></Route>
                        ))
                    }
                </BrowserRouter>
                {content}
            </div>


        );
    }
}

export default App;