import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import links from '../links';
import menus from '../menus';

class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    render() {
        return (

            <header id="header" className="header header-style1">
                {!this.props.activeWallet && <button className="button connect-wallet">CONNECT WALLET</button>}
                <div className="container">
                    <div className="flex-header d-flex justify-content-between align-items-center">
                        <div className="content-menu d-lg-flex">
                            <div className="nav-wrap">
                                <nav id="mainnav" className="hide-on-mobile">
                                    <ul className="menu ace-responsive-menu" data-menu-style="horizontal">
                                        {
                                            menus.map(menu => (
                                                <li key={menu.id}>
                                                    <Link to={menu.tomenu} className={menu.id === 1 ? "active" : ""}>
                                                        {menu.namemenu}
                                                    </Link>
                                                </li>
                                            ))
                                        }
                                        {
                                            links.map(data => (
                                                <li key={data.id}>
                                                    <a href={data.tolink} target={'_blank'}>
                                                        {data.namelink}

                                                    </a>
                                                </li>
                                            ))
                                        }
                                    </ul>
                                </nav>
                            </div>
                        </div>
                        <div dir="rtl" className="btn-menu mobile-header__menu-button">
                            <div className="line line-1"/>
                            <div className="line line-2"/>
                            <div className="line line-3"/>
                            <div className="line line-4"/>
                        </div>
                    </div>
                </div>
            </header>
        );
    }
}

export default Header;
