import React, {Component} from 'react';
import liquidLogo from "../../assets/images/fog-logo.png";
import BigNumber from "bignumber.js";

class Fog extends Component {
    constructor(props) {
        super(props);
        this.onInputChange = this.onInputChange.bind(this);
    }

    state = {
        amount: 0,
        maxStake: 0,
        maxRewards: 0,
        lpStaking: 0,
        llpStaked: 0,
        lpToStake: 0,
        llpToWithdraw: 0,
        rewardsToClaim: 0,

    }

    onInputChange(event) {
        this.setState({
            [event.target.name]: event.target.value
        });
    }

    updateInput(event, targetInput, targetValue) {
        let valueToMax = new BigNumber(window.web3.utils.fromWei(this.props[targetValue].toString(), 'ether'), 10);
        //valueToMax = valueToMax.toFormat(3);
        console.log(targetInput, this.state[targetInput], valueToMax);
        this.setState({
            [targetInput]: valueToMax
        });
        this.onInputChange(event)
    }


    render() {
        BigNumber.config({DECIMAL_PLACES: 3})

        let rewardPool = window.web3.utils.fromWei(this.props.llpRewardPool.toString(), 'ether');
        let liquidLpBalance = new BigNumber(window.web3.utils.fromWei(this.props.liquidLpBalance.toString(), 'ether'), 10);
        let llpStaked = new BigNumber(window.web3.utils.fromWei(this.props.llpYourStakeAmount.toString(), 'ether'), 10);
        let fogBal = new BigNumber(window.web3.utils.fromWei(this.props.fogBalance.toString(), 'ether'), 10);
        let fogDensityValue = new BigNumber(window.web3.utils.fromWei(this.props.fogDensity.toString(), 'ether'), 10);
        let llpRewards = new BigNumber(window.web3.utils.fromWei(this.props.llpReward.toString(), "ether"), 10);

        let rewardPoolCalc = (rewardPool > 90000) ? rewardPool - 90000 : 90000 - rewardPool;
        let roundedRewardPool = new BigNumber(rewardPoolCalc);
        let printRewardPool = new BigNumber(rewardPool);

        return (

            <div>
                <div id="fog-container">
                    <div className="info">
                        <img className="logo-top logo-top-smaller" src={liquidLogo} alt="FOG logo"/>
                    </div>

                    <section className="pools" id="pools">

                        <h2 className="shadow-text">FOG</h2>

                        <div className="pool is-farming fog">
                            <div className="pool-inner">
                                <div className="pool-info">
                                    <div className="condensate">
                                        <div className="condensate-panel">
                                            <img className="condensate-logo" src={liquidLogo} alt="Liquid logo"/>
                                            <div className="condensate-label">
                                                Claimable LP Rewards
                                            </div>
                                            <div className="condensate-info">
                                                {fogDensityValue.toFormat(3)} LIQ-ETH UNI LP
                                            </div>
                                            <button className="condensate-button button"
                                            onClick={event => {
                                                event.preventDefault();
                                                this.props.condensate()
                                            }}
                                            >CONDENSATE</button>
                                        </div>
                                    </div>
                                    <div id="pool-stats">
                                        <h3 id="stats-1" className="shadow-text">STATS</h3>
                                        <div className="stats-inner">

                                            <div className="stats-panel">
                                                <div className="stats-label">
                                                    Your LIQ UNIV2 Balance
                                                </div>
                                                <div className="stats-info">
                                                    {liquidLpBalance.toFormat(2)}
                                                </div>
                                            </div>
                                            <div className="stats-panel">
                                                <div className="stats-label">
                                                    Your total stake
                                                </div>
                                                <div className="stats-info">
                                                    {llpStaked.toFormat(2)} LIQ UNIV2
                                                </div>
                                            </div>
                                            <div className="stats-panel">
                                                <div className="stats-label">
                                                    Your FOG Balance
                                                </div>
                                                <div className="stats-info">
                                                    {fogBal.toFormat(3)}
                                                </div>
                                            </div>
                                            <div className="stats-panel">
                                                <div className="stats-label">
                                                    Pending FOG Rewards
                                                </div>
                                                <div className="stats-info">
                                                    {llpRewards.toFormat(3)}
                                                </div>
                                            </div>
                                            <div className="stats-panel">
                                                <div className="stats-label">
                                                    Total FOG Rewarded
                                                </div>
                                                <div className="stats-info">
                                                    {roundedRewardPool.toFormat(3)}
                                                </div>
                                            </div>
                                            <div className="stats-panel">
                                                <div className="stats-label">
                                                    FOG Rewards Remaining
                                                </div>
                                                <div className="stats-info">
                                                    {printRewardPool.toFormat(3)}
                                                </div>
                                            </div>
                                            <div className="stats-panel rank">
                                                <div className="stats-label">
                                                    Rank
                                                </div>
                                                <div className="stats-info">
                                                    {this.props.llpRank.toString()}
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="deposit-withdraw">
                                        <h3  className="shadow-text">DEPOSIT/WITHDRAW</h3>

                                        <div id="pool-w-d">
                                            <div className="lp-tokens lp-deposit">
                                                <div className="section-label">Deposit LIQ/ETH LP TOKENS</div>
                                                <div className="token-input">
                                                    <input
                                                        max={liquidLpBalance}
                                                        value={this.state.lpStaking}
                                                        type="number" id="lpStaking" name="lpStaking"
                                                        onChange={event =>
                                                            this.onInputChange(event)
                                                        }
                                                    />
                                                    <button className="max button"
                                                            onClick={(event) => {
                                                                event.preventDefault();
                                                                const targetValue = 'liquidLpBalance';
                                                                const targetInput = 'lpStaking';
                                                                this.updateInput(event, targetInput, targetValue);
                                                            }}
                                                    >MAX
                                                    </button>
                                                </div>
                                                <button className="button btn-full"
                                                        onClick={(event) => {
                                                            event.preventDefault();
                                                            let amount;
                                                            amount = this.state.lpStaking.toString();
                                                            amount = window.web3.utils.toWei(amount, "Ether");
                                                            this.props.stakeTokens(amount);
                                                        }}

                                                >Deposit
                                                </button>
                                            </div>

                                            <div className="lp-tokens lp-withdraw">
                                                <div className="section-label">Withdraw LIQ/ETH LP TOKENS</div>
                                                <div className="token-input">
                                                    <input
                                                        max={llpStaked}
                                                        value={this.state.llpStaked}
                                                        type="number" id="llpStaked" name="llpStaked"
                                                        onChange={event =>
                                                            this.onInputChange(event)
                                                        }
                                                    />
                                                    <button className="max button"
                                                            onClick={(event) => {
                                                                event.preventDefault();
                                                                const targetValue = 'llpYourStakeAmount';
                                                                const targetInput = 'llpStaked';
                                                                this.updateInput(event, targetInput, targetValue);
                                                            }}
                                                    >MAX
                                                    </button>
                                                </div>

                                                <button className="button btn-full"
                                                        onClick={(event) => {
                                                            event.preventDefault();
                                                            let amount;
                                                            amount = this.state.llpStaked.toString();
                                                            amount = window.web3.utils.toWei(amount, "Ether");
                                                            this.props.unstakeTokens(amount);
                                                        }}
                                                >Withdraw
                                                </button>
                                            </div>


                                            <div className="rewards">
                                                <div className="section-label">REWARDS</div>
                                                <div className="token-input">
                                                    <input readOnly={true} value={llpRewards}
                                                           type="number" id="llpRewards" name="maxRewards"/>
                                                    <button className="max button"
                                                            onClick={(event) => {
                                                                this.props.claimRewards()
                                                            }}
                                                    >Claim
                                                    </button>
                                                </div>

                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>

                </div>
            </div>);
    }
}

export default Fog;
